/* eslint-disable no-unused-vars */
<template>
  <CRow class="justify-content-center">
    <CCol>
      <CCard>
        <CCardHeader class="d-flex justify-content-between">
          <div>
            <slot name="header">
              <CIcon name="cil-healing" />
              <strong>Categories</strong>
            </slot>
          </div>
          <CButton
            variant="outline"
            color="success"
            @click="openModalNewCategory()"
          >
            <CIcon name="cil-plus" class="mt-0" />&nbsp;Add New Category
          </CButton>
        </CCardHeader>
        <CCardBody>
          <v-client-table :data="rows" :columns="columns" :options="options">
            <div slot="actions" slot-scope="{ row }">
              <CButton
                class="mr-3"
                variant="outline"
                @click="openModalEdit(row)"
                color="primary"
              >
                Edit
                <CIcon name="cil-pencil" />
              </CButton>
              <CButton
                class="mr-3"
                variant="outline"
                @click="deleteRow(row)"
                color="danger"
              >
                Delete
                <CIcon name="cil-delete" />
              </CButton>
            </div>
          </v-client-table>
        </CCardBody>
      </CCard>
    </CCol>
    <sweet-modal :blocking="Boolean(true)" ref="modal" overlay-theme="dark">
      <CCard style="text-align: left; border: 1px solid">
        <CCardHeader>
          <strong>New Category Form</strong>
        </CCardHeader>
        <CForm @submit.prevent="submit">
          <CCardBody>
            <p
              v-for="(error, index) in errors"
              :key="index"
              class="alert alert-danger"
            >
              {{ error }}
            </p>
                <CInput
                  :class="{ 'form-group--error': $v.category.$error }"
                  label="Category"
                  v-model.trim="$v.category.$model"
                />
                <div class="error" v-if="!$v.category.required">
                  Category is required
                </div>
          </CCardBody>
          <CCardFooter>
            <CButton
              class="col"
              variant="outline"
              color="success"
              type="submit"
            >
              <CIcon name="cil-check-circle" />&nbsp;Submit
            </CButton>
          </CCardFooter>
        </CForm>
      </CCard>
    </sweet-modal>
  </CRow>
</template>
<script>
import { required } from "vuelidate/lib/validators";
export default {
  name: "Categories",
  data() {
    return {
      category: "",
      formData: {},
      rows: [],
      errors: [],
      isEdit: false,
      id: "",
      columns: ["name", "actions"],
      options: {
        columnsClasses: {
          actions: "text-center",
          short_name: "text-center",
        },
        perPage: 10,
      },
    };
  },
  validations: {
    category: {
      required,
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.$http
        .get("http://localhost:3000/category/get")
        .then((res) => {
          this.rows = res.data;
          console.log(res.data);
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
    },
    deleteRow(row) {
      this.$http
        .delete("http://localhost:3000/category/" + row._id)
        .then((res) => {
          this.getData();
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
    },
    submit() {
      this.$v.$touch()
      if (this.$v.$invalid) {
        this.errors.push("Please check the form and submit.");
      } else {
        this.formData = {
          name: this.category,
          short_name: this.short_name,
        };
        if (this.isEdit === false) {
          this.$http
            .post("http://localhost:3000/category/post", this.formData)
            .then((res) => {
              this.$swal.fire("Done!", "Category created successfully!", "success");
              this.getData();
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              this.$swal.fire("Values have errors", err.response.data.errors[0].msg || "Something went wrong.", "error");
              console.log(err.response);
            });
        } else {
          this.$http
            .put("http://localhost:3000/category/" + this.id, this.formData)
            .then((res) => {
              this.$swal.fire("Done!", "Category edited successfully!", "success");
              this.getData();
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              this.$swal.fire("Values have errors", err.response.data.errors[0].msg || "Something went wrong.", "error");
              console.log(err.response);
            });
        }
        this.$refs.modal.close("modal");
      }
    },
    async openModalEdit(row) {
      this.isEdit = true;
      this.id = row._id;
      this.$refs.modal.open("modal");
      this.category = row.name;
      this.short_name = row.short_name;
    },
    async openModalNewCategory(row) {
      this.isEdit = false;
      this.$refs.modal.open("modal");
      this.category = "";
      this.short_name = "";
    },
  },
};
</script>